@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion{

    display: flex;
    flex-direction: column;
    @include m.desktop {
         flex-direction: row; 
    }

    &__enlace{
        color: v.$blanco;
        font-size: 1.5rem;
        padding: 2rem;
        font-weight: v.$black;
        text-transform: uppercase;
        text-align: center;

        &:hover{
            background-color: v.$blanco;
            color: v.$azul;
        }

        &--actual{
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}